@import "../utils/_variables.scss";

.card {
  width: 200px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  font-family: Roboto, sans-serif;
  padding: 32px 16px 16px;

  &__image {
    height: 134px;
    margin: 0 auto 40px;
    display: block;
    object-fit: cover;
    object-position: center;
    left: 0;
    right: 0;
  }

  &__title {
    margin-bottom: 4px;
    line-height: 18px;
    font-size: 12px;
    font-weight: 500;
    color: $main_text_color;
  }

  &__code {
    margin: 0 auto 16px;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: $small_text_color;
  }

  &__rating {
    margin: 0 auto 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__price {

    margin: 0 auto 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__link {
    height: 40px;
    display: block;
    text-align: center;
    background: $second_text_color;
    border-radius: 5px;
    border: 1px solid $second_text_color;
    line-height: 40px;
    font-weight: 700;
    font-size: 14px;
    text-transform: uppercase;
    color: #fff;
    transition: background-color 0.2s, color 0.2s;

    &--hover:hover {
      background-color: #fff;
      color: $second_text_color;
    }
  }

  &__reviews {
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: $main_text_color;
  }

  &__price-label {
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: $small_text_color;
  }

  &__price-amount {
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    color: $main_text_color;
  }
}
